import { createMarkdownPlatePlugin, Modal, useEditor, useEditorActions } from 'topwrite';
import { Container } from '@/components/html/link';
import { ReactComponent as Icon } from '../../images/link.svg';
import { ReactComponent as EditIcon } from '../../images/edit.svg';

export const LinkPlugin = createMarkdownPlatePlugin({
    voidTypes: 'leaf:link',
    renderElement() {
        return {
            'leaf:link': ({ element, attributes, children, ElementMenu }) => {
                const { Node, findPath, setNodes, insertText } = useEditorActions();
                const title = Node.string(element);
                const url = element.attributes.url!;
                const editor = useEditor();

                return <ElementMenu
                    contentEditable={false}
                    element={element}
                    {...attributes}
                    items={[
                        {
                            text: '编辑',
                            icon: <EditIcon />,
                            async onSelect() {
                                const result = await Modal.prompt<{ url: string, title: string }>({
                                    title: '编辑链接',
                                    schema: {
                                        type: 'object',
                                        properties: {
                                            url: {
                                                type: 'string',
                                                title: '链接'
                                            },
                                            title: {
                                                type: 'string',
                                                title: '标题'
                                            }
                                        },
                                        required: ['url']
                                    },
                                    formData: {
                                        title,
                                        url
                                    }
                                });

                                if (result) {
                                    const path = findPath(editor, element);

                                    setNodes(editor, {
                                        attributes: {
                                            url: result.url,
                                        }
                                    }, { at: path });

                                    if (result.title) {
                                        insertText(editor, result.title, { at: path, voids: true });
                                    }
                                }
                            }
                        }
                    ]}
                >
                    <Container href={url}>
                        <Icon /><span>{title || url}</span>
                    </Container>
                    {children}
                </ElementMenu>;
            }
        };
    }
});
