import { useEffect } from 'react';
import { GetComponentProps, useActions } from 'topwrite';
import { TabsPlugin } from '@/components/plugins/tabs-plugin';
import { CollapsePlugin } from '@/components/plugins/collapse-plugin';
import { ColumnsPlugin } from '@/components/plugins/columns-plugin';
import { LinkPlugin } from '@/components/plugins/link-plugin';
import { CatalogPlugin } from '@/components/plugins/catalog-plugin';


export default function Activate({ children }: GetComponentProps<'activate'>) {
    const { addPlugin, removePlugin } = useActions('editor');
    useEffect(() => {

        addPlugin(TabsPlugin);
        addPlugin(CollapsePlugin);
        addPlugin(ColumnsPlugin);
        addPlugin(LinkPlugin);
        addPlugin(CatalogPlugin);
        return () => {
            removePlugin(TabsPlugin);
            removePlugin(CollapsePlugin);
            removePlugin(ColumnsPlugin);
            removePlugin(LinkPlugin);
            removePlugin(CatalogPlugin);
        };
    }, []);
    return children;
}
