import {
    createContext,
    MouseEventHandler,
    PropsWithChildren,
    useCallback,
    useContext,
    useEffect,
    useMemo, useRef,
    useState
} from 'react';
import { createMarkdownPlatePlugin, useEditor, useEditorActions } from 'topwrite';
import { Container, Content, Nav, NavItem, Pane } from '../html/tabs';
import { ReactComponent as AddIcon } from '@/images/add.svg';
import { ReactComponent as DeleteIcon } from '@/images/delete.svg';
import useMergedRef from '@/utils/use-merged-ref';

const TabContext = createContext<{
    nav: HTMLDivElement | null,
    current: number,
    setCurrent: (index: number) => void
} | undefined>(undefined);

const TabContextProvider = ({
    nav,
    total,
    children
}: PropsWithChildren<{ nav: HTMLDivElement | null, total: number }>) => {
    const [current, setCurrent] = useState(1);

    useEffect(() => {
        if (current > total) {
            setCurrent(total);
        }
    }, [total]);

    const value = useMemo(() => {
        return { nav, current, setCurrent };
    }, [nav, current]);

    return <TabContext.Provider value={value}>
        {children}
    </TabContext.Provider>;
};

const useTabContext = () => {
    const context = useContext(TabContext);

    if (!context) {
        throw new Error;
    }
    return context;
};

const {
    insertNodes, findPath, removeNodes, Element, Node,
    isContainerDirective,
    isDirectiveLabel,
} = useEditorActions();

export const TabsPlugin = createMarkdownPlatePlugin({
    voidTypes: ['label:tabs'],
    renderElement(editor) {
        return {
            'container:tabs': ({ element, attributes, children, ElementMenu }) => {
                const [nav, setNav] = useState<HTMLDivElement | null>(null);
                const editor = useEditor();
                const path = findPath(editor, element);

                const total = element.children.length - 1;
                return <ElementMenu
                    {...attributes}
                    element={element}
                    items={[
                        {
                            text: '增加一栏',
                            icon: <AddIcon />,
                            onSelect() {
                                insertNodes(editor, {
                                    type: 'containerDirective',
                                    name: 'tab',
                                    attributes: {},
                                    children: [{ type: 'directiveLabel', name: 'tab', children: [{ text: '新标签' }] }]
                                }, { at: path.concat(element.children.length) });
                            }
                        },
                        {
                            text: '删除一栏',
                            icon: <DeleteIcon />,
                            onSelect() {
                                removeNodes(editor, { at: path.concat(element.children.length - 1) });
                            }
                        }
                    ]}
                >
                    <Container>
                        <Nav ref={useCallback(ref => setNav(ref), [])} />
                        <TabContextProvider nav={nav} total={total}>
                            <Content>{children}</Content>
                        </TabContextProvider>
                    </Container>
                </ElementMenu>;
            },
            'container:tab': ({ element, attributes, children }) => {
                const { current } = useTabContext();
                const { findPath } = useEditorActions();
                const path = findPath(editor, element);

                const index = path[path.length - 1];
                const active = index === current;

                return <Pane {...attributes} active={active}>
                    {children}
                </Pane>;
            },
            'label:tab': ({ element, attributes, children }) => {
                const { current, setCurrent, nav } = useTabContext();
                const { findPath } = useEditorActions();
                const path = findPath(editor, element);
                const ref = useRef<HTMLDivElement>(null);

                const index = path[path.length - 2];
                const active = index === current;

                const onMouseDown = useCallback<MouseEventHandler>((e) => {
                    if (!active) {
                        e.preventDefault();
                        setCurrent(index);
                    }
                }, [index, active, setCurrent]);

                useEffect(() => {
                    const dom = ref.current;
                    if (nav && dom) {
                        nav.appendChild(dom);
                        return () => {
                            nav.removeChild(dom);
                        };
                    }
                }, [nav]);

                return <NavItem
                    {...attributes}
                    ref={useMergedRef(ref, attributes.ref)}
                    contentEditable={!active ? false : undefined}
                    active={active}
                    onMouseDown={onMouseDown}>
                    {children}
                </NavItem>;
            }
        };
    },
    withOverrides(editor) {
        const { normalizeNode } = editor;
        editor.normalizeNode = (entry) => {
            const [node, path] = entry;

            if (isContainerDirective(node, 'tabs')) {
                //删除空
                if (!Element.isElementList(node.children)) {
                    removeNodes(editor, { at: path });
                    return;
                }
                for (const [child, childPath] of Node.children(editor, path)) {
                    if (!isContainerDirective(child, 'tab') && !isDirectiveLabel(child, 'tabs')) {
                        removeNodes(editor, { at: childPath });
                    }
                }
            }

            if (isContainerDirective(node, 'tab')) {
                const parent = Node.parent(editor, path);
                if (!isContainerDirective(parent, 'tabs')) {
                    removeNodes(editor, { at: path });
                    return;
                }

                if (node.children.length === 1) {
                    insertNodes(editor, {
                        type: 'paragraph',
                        children: [{ text: '' }]
                    }, { at: path.concat(1) });

                    entry = [Node.get(editor, path), path];
                }
            }

            normalizeNode(entry);
        };
        return editor;
    }
});
