import { createMarkdownPlatePlugin, styled, useEditorActions } from 'topwrite';
import { Content } from '@/components/html/catalog';

export const CatalogPlugin = createMarkdownPlatePlugin({
    voidTypes: 'leaf:catalog',
    renderElement(editor) {
        return {
            'leaf:catalog': ({ element, attributes, children, ElementMenu }) => {
                const all = element.attributes.all !== undefined;

                return <ElementMenu
                    contentEditable={false}
                    element={element}
                    items={[
                        {
                            text: all ? '显示子目录' : '显示所有目录',
                            onSelect() {
                                const { setNodes, findPath } = useEditorActions();
                                const path = findPath(editor, element);
                                setNodes(editor, {
                                    attributes: {
                                        all: all ? undefined : ''
                                    }
                                }, { at: path });
                            }
                        }
                    ]}
                    {...attributes}
                >
                    <Container>
                        <div>
                            <Content all={all} />
                        </div>
                    </Container>
                    {children}
                </ElementMenu>;
            }
        };
    }
});

const Container = styled.div`
  min-height: 50px;
  position: relative;
  margin: 1em 0;
  padding: .5em;
  display: flex;
  align-items: center;
  flex-direction: row;

  & > div {
    width: 100%;
    filter: blur(1px);

    & > ul:last-child {
      margin: 0;
    }
  }

  &:after {
    content: '目录卡片';
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    font-size: 20px;
    color: #727981;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
  }

  h2 {
    padding-bottom: 0.4em;
    font-size: 1.3em;
    line-height: 1.2;
    border-bottom: 1px solid rgb(238, 238, 238);
  }

  ul {
    li {
      a {
        text-decoration: none;
        color: var(--bs-primary);
      }
    }
  }
`;
