import { createMarkdownPlatePlugin, useEditorActions } from 'topwrite';
import { useCallback, useMemo } from 'react';
import { Container, Content, Controller, Summary } from '../html/collapse';

export const CollapsePlugin = createMarkdownPlatePlugin({
    renderElement(editor) {
        return {
            'container:collapse': ({ element, attributes, children, ElementMenu }) => {
                const open = element.attributes.close == undefined;

                const { setNodes, findPath } = useEditorActions();

                const onMouseDown = useCallback(() => {
                    const path = findPath(editor, element);
                    setNodes(editor, {
                        attributes: {
                            close: open ? '' : undefined
                        }
                    }, { at: path });
                }, [element, open]);

                return <ElementMenu element={element} {...attributes}>
                    <Container $open={open}>
                        <Controller open={open} contentEditable={false} onClick={onMouseDown} />
                        <Content>
                            {children}
                        </Content>
                    </Container>
                </ElementMenu>;
            },
            'label:collapse': ({ element, attributes, children }) => {
                const { Node } = useEditorActions();

                const placeholder = useMemo(() => {
                    return Node.string(element) ? undefined : '请输入折叠卡片标题';
                }, [element]);

                return <Summary {...attributes} data-placeholder={placeholder}>
                    {children}
                </Summary>;
            }
        };
    },
    withOverrides(editor) {
        const { normalizeNode } = editor;
        editor.normalizeNode = (entry) => {
            const [node, path] = entry;
            const {
                isContainerDirective,
                insertNodes,
            } = useEditorActions();

            if (isContainerDirective(node, 'collapse')) {
                if (node.children.length === 1) {
                    insertNodes(editor, {
                        type: 'paragraph',
                        children: [{ text: '' }]
                    }, { at: path.concat(1) });
                }
            }
            normalizeNode(entry);
        };
        return editor;
    }
});
