import { Anchor, GetComponentProps, SummaryArticle, SummaryPart, useBook, useFile } from 'topwrite';
import { Fragment } from 'react';

function renderArticle(article: SummaryArticle) {
    return <li key={article.level.toString()}>
        <Anchor href={article.ref}>{article.title}</Anchor>
        {renderArticles(article.children)}
    </li>;
}

function renderArticles(articles: SummaryArticle[]) {
    if (articles.length === 0) {
        return null;
    }
    return <ul>
        {articles.map(article => renderArticle(article))}
    </ul>;
}

function renderPart(part: SummaryPart) {
    return <Fragment key={part.level.toString()}>
        <h2>{part.title}</h2>
        {renderArticles(part.articles)}
    </Fragment>;
}

interface Props {
    all: boolean;
}

export const Content = ({ all }: Props) => {
    const { summary } = useBook();

    const file = useFile();

    if (all) {
        if (summary.isSinglePart()) {
            return renderArticles(summary.getLastPart().articles);
        }
        return <>
            {summary.parts.map(part => renderPart(part))}
        </>;
    }

    const article = summary.getArticle(article => article.path === file.path || article.ref === file.path);
    if (!article) {
        return null;
    }
    return renderArticles(article.children);
};


export default function Catalog({ props: { all } }: GetComponentProps<'leaf-directive'>) {
    return <Content all={all !== undefined} />;
}
