import { createMarkdownPlatePlugin, styled, useEditor, useEditorActions } from 'topwrite';
import { Column, Container } from '@/components/html/columns';
import { ReactComponent as AddIcon } from '@/images/add.svg';
import { ReactComponent as DeleteIcon } from '@/images/delete.svg';

const {
    insertNodes, findPath, removeNodes, Element, Node,
    isContainerDirective,
    isDirectiveLabel,
} = useEditorActions();

export const ColumnsPlugin = createMarkdownPlatePlugin({
    voidTypes: ['label:columns', 'label:column'],
    renderElement() {
        return {
            'container:columns': ({ children, element, attributes, ElementMenu }) => {
                const editor = useEditor();
                const path = findPath(editor, element);

                return <ElementMenu
                    {...attributes}
                    element={element}
                    items={[
                        {
                            text: '增加一列',
                            icon: <AddIcon />,
                            onSelect() {
                                insertNodes(editor, {
                                    type: 'containerDirective',
                                    name: 'column',
                                    attributes: {},
                                    children: [{ text: '' }]
                                }, { at: path.concat(element.children.length) });
                            }
                        },
                        {
                            text: '删除一列',
                            icon: <DeleteIcon />,
                            onSelect() {
                                removeNodes(editor, { at: path.concat(element.children.length - 1) });
                            }
                        }
                    ]}
                >
                    <StyledContainer columns={element.children.length - 1}>
                        {children}
                    </StyledContainer>
                </ElementMenu>;
            },
            'container:column': ({ attributes, children }) => {
                return <Column {...attributes}>
                    {children}
                </Column>;
            }
        };
    },
    withOverrides(editor) {
        const { normalizeNode } = editor;
        editor.normalizeNode = (entry) => {
            const [node, path] = entry;

            if (isContainerDirective(node, 'columns')) {
                //删除空列表
                if (!Element.isElementList(node.children)) {
                    removeNodes(editor, { at: path });
                    return;
                }

                for (const [child, childPath] of Node.children(editor, path)) {
                    if (!isContainerDirective(child, 'column') && !isDirectiveLabel(child, 'columns')) {
                        removeNodes(editor, { at: childPath });
                    }
                }
            }
            if (isContainerDirective(node, 'column')) {
                const parent = Node.parent(editor, path);
                if (!isContainerDirective(parent, 'columns')) {
                    removeNodes(editor, { at: path });
                    return;
                }

                if (node.children.length === 1) {
                    insertNodes(editor, {
                        type: 'paragraph',
                        children: [{ text: '' }]
                    }, { at: path.concat(1) });
                }
            }

            normalizeNode([Node.get(editor, path), path]);
        };

        return editor;
    }
});

const StyledContainer = styled(Container)`
  ${Column} {
    outline: 1px dashed #e0e0e0;
  }
`;

